import React, { useEffect, useState } from "react";
import { Route, Routes as AllRoutes } from "react-router-dom";
import UserMainPage from "./pages/users";
import { ROLE } from "./helpers/constants"
import { useDispatch, useSelector } from "react-redux";
import { REDUCER_TYPES } from "./store";
import { resetAuth } from "./store/api/utils";
import Error from "./pages/error";
import GroupsPage from './pages/groups'
import OgProfile from './pages/og_profile';
import Test from "./pages/test"
import TestUpload from "./pages/uploadTest";
import SkillPage from "./pages/skills";
import QuestionBank from "./pages/questionBank";
import SkillMatrix from "./pages/skillMatrix"
import TestPortal from "./pages/testPortal"
import TrainingPage from "./pages/planTraining"
import TestDetail from "./pages/test/testDetail"
import AddReport from "./pages/report"
import Op_ChangePassword from './pages/Profile'
import ChangeLanguage from './pages/ChangeLanguage'

const Routes = () => {

  const dispatch = useDispatch();
  const [hydrated, setHydrated] = useState(false);
  const state = useSelector((state) => state[REDUCER_TYPES.AUTH]);
  const role = state?.user?.data?.role
  
  const routes = [
    {
      path: '/change-password',
      element: <Op_ChangePassword />
    },
    {
      path: '/change-language',
      element: <ChangeLanguage />
    },
    {
      path: '/report',
      element: <AddReport />
    },
    {
      path: '/TestDetail',
      element: <TestDetail />
    },
    {
      path: '/plan-training',
      element: <TrainingPage/>
    },
    {
      path: '/test-portal',
      element: <TestPortal />
    },
    {
      path: '/skill-matrix',
      element: <SkillMatrix />
    },
    {
      path: '/test',
      element: <Test />
    },
    {
      path: '/test-upload',
      element: <TestUpload />
    },
    {
      path: '/skill-page',
      element: <SkillPage />
    },
    {
      path: '/question-bank',
      element: <QuestionBank />
    },
    {
      path: '/group',
      element: <GroupsPage />
    },
    {
      path: '/error',
      element: <Error />
    },
    {
      path: "/users",
      element: role === ROLE.ADMINISTRATOR ? <UserMainPage /> : <Error />,
    },
    {
      path: "/profile",
      element: role === ROLE.ADMINISTRATOR ? <OgProfile /> : <Error />
    },
  ];
  useEffect(() => {
    resetAuth(dispatch, state);
    setHydrated(true);
  }, []);

  if (!hydrated) {
    return null;
  }
  return (
    <AllRoutes>
      {routes.map(({ path, element }) => (
        <Route element={element} path={path} key={path} />
      ))}
    </AllRoutes>

  );
};

export default Routes;
